/* Common styles for all screen sizes */

.header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: rgb(43, 42, 42);
  height: 5vh;
}

.header div {
  font-size: 20px;
  font-weight: bold;
}

.IMAGE-1-menu {
  position: relative;
  height: 120vh;
  image-rendering: -webkit-optimize-contrast;
}

a {
  color: white;
}

.content-menu {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  height: 100vh;
  width: 100%;
}

.content-menu span.img-text-menu {
  background-color: #333;
  text-transform: uppercase;
  color: #fff;
  padding: 1rem;
  font-size: 1.5rem;
  letter-spacing: 10px;
}

.h3 {
  letter-spacing: 6px;
  text-transform: uppercase;
  font: 1.3rem;
  text-align: center;
}

.kategoria {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap; /* Allow items to wrap onto multiple lines */
}

.njeProve {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10px; /* Added margin for better spacing on smaller screens */
}

.njeProve img {
  width: 100%; /* Adjusted width for responsiveness */
  height: auto; /* Maintain aspect ratio */
  max-height: 70vh; /* Limit maximum height */
}

.njeProve h2 {
  font-size: 2.5rem;
}

.njeProve h1 {
  font-size: 2.5rem;
}

.njeProve {
  position: relative;
}

.njeProve button {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #3498db;
  color: #fff;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  font-size: 2rem;
  font-weight: bold;
}

.njeProve:hover button {
  opacity: 1;
}

.chef-section {
  text-align: center;
  padding: 20px;
  background-color: #f8f8f8;
  border-radius: 10px;
  margin-top: 20px;
}

.chef-image {
  width: 100%;
  height: auto;
  max-height: 50vh;
  object-fit: cover;
  border-radius: 0%;
  margin-bottom: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.chef-section h2 {
  font-size: 1.5em;
  color: #333;
  font-family: 'Poppins', sans-serif;
}

.chef-section p {
  font-size: 1em;
  color: #666;
  font-family: 'Poppins', sans-serif;
  font-weight: bold;
}

/* Media queries for responsiveness */
@media (max-width: 768px) {
  .njeProve img {
    max-height: none;
  }

  .chef-image {
    max-height: 40vh;
  }
}
